

@mixin tablet-land() {
  @media (max-width: 1250px) {
    @content;
  }
}

@mixin tablet-port() {
  @media (max-width: 840px) {
    @content;
  }
}

@mixin mobile-land() {
  @media (max-width: 780px) {
    @content;
  }
}

@mixin mobile-port() {
  @media (max-width: 470px) {
    @content;
  }
}