.mainContainer {
  color: $font;
  width: 100%;
  height: auto;
  min-height: 450px;
  padding: 2.5%;
  // border: 4px solid $accent;
  @include mobile-land {
    margin-bottom: 100px;
    border: 4px solid rgba(255, 255, 255, 0);
  }
  button {
    width: 100%;
    height: 90px;
    font-size: 2.5rem;
    bottom: 0;
    right: 0;
    margin: 0 auto;
  }
}

button.oneWay {
  width: 100%;
}

button.twoWay {
  width: 50%;
}

.next {
  border-left: none;
}
